import React from "react"
import { BriefConfig, BriefReport, BriefReportCompanyReport } from "@rp/react/tanstack-api/brief"
import { LoadingSpinner } from "@rp/react/web/common/base-components"
import { Box } from "@rp/react/web/mui/core"
import { BriefReportBody } from "./BriefReportBody"
import { BriefReportNotGenerated } from "./BriefReportBody/BriefReportNotGenerated"
import { BriefReportHeader } from "./BriefReportHeader"
import { BriefReportNoNews } from "./BriefReportNoNews"
import { BriefReportSubheader } from "./BriefReportSubheader"

type BriefReportRenderProps = {
  briefReport: BriefReport | null
  isBriefReportRetrying: boolean
  briefConfig: BriefConfig | null
  onAddPrompt: (text: string, entityInfo?: BriefReportCompanyReport["entityInfo"]) => void
}

const BriefReportRender = ({
  briefReport,
  briefConfig,
  isBriefReportRetrying,
  onAddPrompt,
}: BriefReportRenderProps) => {
  const title = isBriefReportRetrying
    ? ""
    : briefReport?.reportTitle || briefConfig?.briefName || ""

  const hasBriefReport = !!briefReport

  const shouldShowNoNewsReport = hasBriefReport && briefReport.isEmpty
  const shouldShowLoader = !hasBriefReport && !isBriefReportRetrying && !shouldShowNoNewsReport
  const shouldShowNotGenerated = isBriefReportRetrying && !hasBriefReport && !shouldShowNoNewsReport
  const shouldShowReport = !shouldShowLoader && !shouldShowNotGenerated && !shouldShowNoNewsReport

  return (
    <Box
      sx={{
        mt: 1,
        width: "100%",
        maxWidth: "750px",
        display: "flex",
        flex: 1,
        flexDirection: "column",
      }}
      key={title}
      role="document"
      aria-label="brief report"
    >
      {shouldShowLoader && <LoadingSpinner />}

      {shouldShowNotGenerated && (
        <BriefReportNotGenerated watchlistId={briefConfig?.watchlistIds[0]} />
      )}

      {shouldShowNoNewsReport && <BriefReportNoNews watchlistId={briefReport?.watchlistId} />}

      {shouldShowReport && (
        <>
          <BriefReportHeader title={title} />

          <BriefReportSubheader reportDate={briefReport?.reportDate} />

          <BriefReportBody
            isBriefReportRetrying={isBriefReportRetrying}
            briefReport={briefReport}
            onAddPrompt={onAddPrompt}
          />
        </>
      )}
    </Box>
  )
}

export { BriefReportRender }
